/*---------------------------------------------------------------------------*
 * Ticket
----------------------------------------------------------------------------*/
// Colores
$c_borde_punteado : #ceddf8;
$c_fondo_barras   : #4380c6;
$c_fondo_total    : #f6f6f6;
$c_fondo_tiempo   : #e1ebfc;

// Estilos
.Ticket, .Ticket * {
  box-sizing: border-box;
}
.Ticket {
  // border: 2px dashed $c_borde_punteado;
  display: none;
  clear: both;
  float: none;
  margin: 0;
  overflow: hidden;
  // padding: 0px 10px 10px 10px;

  &.is-visible {
    display: block;
  }

  // --------------------------------------------------------------------------*
  // Estilos para todas las entidades
  // --------------------------------------------------------------------------*
  .strong {
    // font-weight: $w_semibold;
    // color: $c_texto_base;
    &.bold {
      font-weight: $w_bold;
    }
    &.blue {
      color: $c_texto_base_azul;
    }
  }
  .blue {
    color: $c_texto_base_azul;
  }
  &-title {
    color: $c_texto_base_azul;
    font-weight: normal;
    font-size: 15px;
    margin-bottom: 3px;
    // margin-top: 12px;
  }

  &-guia {
    color: $c_texto_base;
    font-size: 13.4px;
    line-height: 1.4;
    margin-bottom: 10px;    
  }
  
  &-guiaInferior {
    padding-top: 15px;
    .guiainferior-content {
      color: $c_texto_base;
      font-size: 13px;
    }
  }

  &-content {
    display: block;
    width: 100%;
    // @include breakpoint(tablet) {
    //   display: table;
    // }
  }

  &-instructions {
    display: block;
    width: 100%;    
    p{
      margin: 4px 0px 10px 0px;
      font-size: 13px;
      color: #135699;
    }
    .row{
      display: block;
      width: 50%;
      float: left;
      margin-bottom: 10px;
      .span--img{
        float: left;
        display: table;
        margin-right: 4px;
        img{
          width: 25px;
        }
      }
      .span--text{
        font-size: 13px;
      }
      .row-one{
        margin-bottom: 10px;
      }
    
      @include mq(tablet_mq) {
        display: block;
        width: 100%;
        float: none;
      }
      
    }
  }

  // Columna de cdigo de barras
  &-codigo {
    background: #c1ddfb;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    text-align: center;
    width: 100%;
    padding: 15px;
    vertical-align: top;
    overflow: hidden;
    color: #135699;
    min-height: 65px;
    height: 100%;
    @include breakpoint(tablet) {
      border-bottom-left-radius: 5px;
      border-top-right-radius: 0;
      display: inline-block;
      // padding: 20px;
      vertical-align: middle;
      width: 33%;
      float: left;
    }

    // span {
    //   color: #fff;
    //   display: block;
    // }
    .row {
      width: 100%;
      display: block;
      // border-top-right-radius: 5px;
      border-top-left-radius: 5px;
      border-bottom-left-radius: 0;
      // padding: 10px 0;
      &.imagen-barras {
        padding: 4px 0px 3px 0px;
        width: 70%;
        margin: 5px auto;
        background: #ffffff;
        border-top-right-radius: 0;
        border-top-left-radius: 0;
        img {
          display: block;
          margin: 0 auto;
        }
      }
      .text {
        // font-size: 14px;
        // // color: #fff;
        font-size: 13px;
        &.strong {
          font-weight: $w_semibold;
        }
        &.large {
          font-size: 30px;
        }
      }
      .title-value{
        margin: 15px 0px 21px 0px;
        font-size: 15px;
      }
      .value{
        margin: 15px 0px 25px 0px;
        font-size: 18px;
        font-weight: 600;
      }
    }

    // --------------------------------------------------------------------------*
    // Filas con fondo de colores distintos
    // --------------------------------------------------------------------------*
    &.nopadding {
      padding: 0;
      .row {
        padding: 10px;
        &--agencia {
          background-color: #c1ddfb;
          // height: 65px;
          .title-value {
            font-size: 13px;
            margin: 0px 0px 5px 0px;
            // padding-top: 12px;
            // padding-bottom: 3px;
          }
          .value {
            font-size: 21px;
            font-weight: $w_semibold;
            margin-top: 0px;
            margin-bottom: 0px;
          }
        }
        &--cuentacorriente {
          background-color: #e1ebfc;
          border-radius: 0;
          min-height: 65px;
          padding: 10px;
          .title-value {
            font-size: 13px;
            margin: 0px 0px 5px 0px;
          }
          .barcode {
            padding: 0 20px;
            img {
              width: 100%;
              max-width: 210px;
            }
          }
          .value {
            margin-top: 0px;
            font-size: 18px;
            font-weight: $w_semibold;
            margin-bottom: 0;
          }
        }
      }
    }
  }

  // Columna de monto Total
  &-total {
    color: $c_texto_base;
    background: $c_fondo_total;
    padding: 20px;
    font-size: 13px;
    width: 100%;
    display: block;
    text-align:center;
    @include breakpoint(tablet) {
      display: inline-block;
      // padding: 10px;
      vertical-align: middle;
      width: 30%;
      float: left;
    }
    &-header {
      margin-bottom: 20px;
      color: $c_texto_base;
    }
    &-detalle {
      .row {
        width: 100%;
        overflow: hidden;
        color: $c_texto_base_azul;
      }
      .label {
        font-size: 15px;
        margin: 13px 0px;
        &.total {
          color: $c_texto_base_azul;
          text-transform: uppercase;
        }
      }
      .value {
        margin:15px 0px;
        font-size: 25px;
        &.total {
          color: $c_texto_base_azul;
        }
      }
    }
  }
  &.dos-columnas {
    .Ticket-total {
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      @include breakpoint(tablet) {
        border-top-right-radius: 0;
        border-bottom-left-radius: 5px;
        width: 50%;
      }
    }
  }

  // Columnda de Tiempo
  &-tiempo {
    background: $c_fondo_tiempo;
    border-radius: 0 0 5px 5px;
    margin: 0;
    padding: 20px;
    vertical-align: middle;
    width: 100%;
    text-align: center;
    @include breakpoint(tablet) {
      border-radius: 0 5px 5px 0;
      display: inline-block;
      // padding: 10px;
      width: 37%;
    }
    .tiempo-mensaje {
      font-size: 13px;
      line-height: 1.3;
      color: $c_texto_base_azul;
      margin-top: 10px;
      // font-weight: 600;
      // margin-bottom: 10px;
      &--bottom {
        margin-top: 20px;
        margin-bottom: 5px;
      }
      span {
        display: block;
        margin-top: 5px;
        &.title-expiracion {
          margin-top: 10px;
        }
        &.text-expiracion {
          font-weight: $w_semibold;
          font-size: 18px;
        }
      }
    }
    .tiempo-reloj {
      padding: 6px 0 0 0;
      width: 100%;
      display: block;
      border-top-right-radius: 0;
      border-bottom-right-radius: 5px;
      border-bottom-left-radius: 5px;
      margin-bottom: 15px;
      .icon-clock {
        background-image: url("../img/reloj.gif");
        display: inline-block;
        height: 25px;
        width: 25px;
        margin-right: 5px;
        vertical-align: middle;
      }
      span {
        color: $c_texto_base_azul;
        font-weight: $w_semibold;
        font-size: 17px;
        line-height: 0.83;
        letter-spacing: -0.5px;
        display: inline-block;
        vertical-align: middle;
        &.divisor {
          padding: 0 3px;
          display: inline-block;
        }
      }
    }

  }

  &.dos-columnas {
    .Ticket-tiempo {
      border-top-right-radius: 0;
      border-bottom-left-radius: 5px;
      @include breakpoint(tablet) {
        border-radius: 0 5px 5px 0;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        // padding: 10px;
      }
    }
  }
  // Caja de trminos y condiciones
  &-terminos {
    margin: 3px 0px;
    font-size: 0;
    display: block;
    color: #7B7B7B;
    text-align:right;
    span {
      // display: block;
      vertical-align: middle;
      font-size: 11px;
      line-height: 1.3;
      display: inline-block;
      margin-left: 3px;
      &:first-child {
        padding-top: 1px;
        vertical-align: top;
        width: 18px;
      }
      @include mq(tablet_mq) {
        font-size: 10px;
      }
    }
    input {
      // vertical-align: middle;
      display: inline-block;
    }
    a {
      text-decoration: underline;
      color: #7B7B7B;
    }
    @include mq(tablet_mq) {
      text-align:center;
    }
  }
}

